
/* HomeJumbotron.js */
.jumbotron{
    background-image: -webkit-gradient(linear, left top, right bottom, color-stop(40%, rgba(68, 160, 141, 0.8)), to(rgba(9, 54, 55, 0.99))), url(../img/jumbotron-bg.jpg);
    background-image: -webkit-linear-gradient(left top, rgba(68, 160, 141, 0.8) 40%, rgba(9, 54, 55, 0.99)), url(../img/jumbotron-bg.jpg);
    background-image: -o-linear-gradient(left top, rgba(68, 160, 141, 0.8) 40%, rgba(9, 54, 55, 0.99)), url(../img/jumbotron-bg.jpg);
    background-image: linear-gradient(to right bottom, rgba(68, 160, 141, 0.8) 40%, rgba(9, 54, 55, 0.99)), url(../img/jumbotron-bg.jpg);
    background-size: cover;
    background-position: top;
    border-radius: 0px !important;
  }
  
.jumbo-text{
  color: #fff;
  margin-bottom: .8rem;
}
.home-body-area{
  margin: 4rem 0;
  padding: 0 3rem;
}
.missions-area{
  margin: 4rem 0;
}
.mission-row{
  margin: 2rem;
}
  .algn-center{
    text-align: center;
  }

/* LessonsTopBar.js */
  .lessons-top-bar-brand{
    padding: .8rem 0rem;
    text-align: center;
    color: rgba(0, 0, 0, 0.9);
  }
  .lessons-top-bar-brand a{
    color: rgba(0, 0, 0, 0.9);
  }
  .lessons-top-bar-brand a:hover {
    color: #28b485;
    -webkit-transition: all .2s;
    -o-transition: all .2s;
    transition: all .2s;
  }

  /* LoginPage.js and Signup.js */
  .auth-page{
    padding: 2rem 0;
  }
  .bg{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    min-height: 70vh;
  }


